import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import './styles/globals.css';
import './i18n/index';

// Modern Components
import ModernHeader from './components/layout/ModernHeader';
import ModernHero from './components/sections/ModernHero';
import Services from './components/sections/Services';
import Blog from './components/sections/Blog';
import About from './components/sections/About';
import Contact from './components/sections/Contact';
import Footer from './components/layout/Footer';

// Pages
import LegalPage from './pages/LegalPage';

// Hooks
import { useSEO } from './hooks/useSEO';

const App: React.FC = () => {
  const { i18n } = useTranslation();
  const [currentPage, setCurrentPage] = useState<string>('home');
  const [legalPageType, setLegalPageType] = useState<string>('');

  // SEO setup
  useSEO({
    title: i18n.language === 'es' 
      ? 'Centro Mdico de Accidentes Automovilsticos de Laredo - Tratamiento Experto'
      : 'Laredo Car Accident Medical Center - Expert Treatment',
    description: i18n.language === 'es'
      ? 'Centro mdico especializado en tratamiento de lesiones por accidentes automovilsticos en Laredo, TX. Atencin quiroprctica, manejo del dolor e imgenes diagnsticas. Personal bilinge.'
      : 'Specialized medical center for car accident injury treatment in Laredo, TX. Chiropractic care, pain management, and diagnostic imaging. Bilingual staff.',
    keywords: i18n.language === 'es'
      ? 'doctor accidentes automovilsticos Laredo, tratamiento lesiones auto Laredo, quiroprctico accidentes Laredo, manejo dolor Laredo'
      : 'Laredo car accident doctor, car accident treatment Laredo, Laredo auto injury, chiropractor car accident Laredo, pain management Laredo',
    lang: i18n.language
  });

  // Language persistence
  useEffect(() => {
    const handleLanguageChange = (lang: string) => {
      localStorage.setItem('preferred-language', lang);
      document.documentElement.lang = lang;
    };

    i18n.on('languageChanged', handleLanguageChange);
    document.documentElement.lang = i18n.language;

    return () => {
      i18n.off('languageChanged', handleLanguageChange);
    };
  }, [i18n]);

  // Simple navigation handler
  const handleNavigation = (page: string, pageType?: string) => {
    setCurrentPage(page);
    if (pageType) {
      setLegalPageType(pageType);
    }
    // Scroll to top
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  const renderPage = () => {
    if (currentPage === 'legal') {
      return <LegalPage page={legalPageType} onNavigateHome={() => handleNavigation('home')} />;
    }

    return (
      <>
        <ModernHeader />
        
        <main id="main-content" role="main">
          <ModernHero />
          <Services />
          <Blog />
          <About />
          <Contact />
        </main>
        
        <Footer onNavigate={handleNavigation} />
      </>
    );
  };

  return (
    <div className="App">
      {/* Skip Links for Accessibility */}
      <a href="#main-content" className="skip-link">
        {i18n.language === 'es' ? 'Saltar al contenido principal' : 'Skip to main content'}
      </a>
      <a href="#navigation" className="skip-link">
        {i18n.language === 'es' ? 'Saltar a la navegacin' : 'Skip to navigation'}
      </a>

      {renderPage()}
    </div>
  );
};

export default App;
